<template>
  <v-main class="back">
    <div>
      <TopNav><span class="left-title">我喜欢去的地方</span></TopNav>
      <v-btn color="rgb(122,33,158)" @click="Return"
             style="color: white;
             float: left;
             margin-left: 5%;
             margin-top: 20%;">
        返回
      </v-btn>
      <v-btn color="rgb(122,33,158)" @click="Next"
             style="color: white;
             float: right;
             margin-right: 4%;
             margin-top: 20%;">
        结束啦
      </v-btn>
      <AllSpots></AllSpots>
    </div>
  </v-main>
</template>

<script>
import TopNav from "../components/TopNav";
import AllSpots from "../components/spots/AllSpots";

export default {
  name: "Entertainment",
  components:{
    AllSpots,
    TopNav,
  },
  data:()=>({
    path1:'/delicacy',
    path2:'/introduce',
  }),
  methods:{
    Return(){
      this.$router.replace(this.path1);
    },
    Next(){
      this.$router.replace(this.path2);
    }
  }
}
</script>

<style scoped>
.left-title{
  color: white;
  font-size: 130%;
  margin-left: 20px;
}
.back{
  background-color: rgb(250,247,243);
  height: 100%;
  width: 100%;
}
</style>



