    ></v-carousel-item>
    <v-carousel-item
      :src="require('../assets/chicken.jpg')" reverse-transition="fade-transition" transition="fade-transition"
    ></v-carousel-item>
    <v-carousel-item
      :src="require('../assets/noodle1.jpg')" reverse-transition="fade-transition" transition="fade-transition"
    ></v-carousel-item>
    <v-carousel-item
      :src="require('../assets/ganzhamogu.jpg')" reverse-transition="fade-transition" transition="fade-transition"
    ></v-carousel-item>
    <v-carousel-item
      :src="require('../assets/beef.jpg')" reverse-transition="fade-transition" transition="fade-transition"
    ></v-carousel-item>
    <v-carousel-item
      :src="require('../assets/noodle2.jpg')" reverse-transition="fade-transition" transition="fade-transition"
    ></v-carousel-item>
    <v-carousel-item
      :src="require('../assets/zhuti.jpg')" reverse-transition="fade-transition" transition="fade-transition"
    ></v-carousel-item>
  </v-carousel>
</template>

<script>
export default {
  name: "Carousels",
}
</script>

