<template>
  <div>
    <v-btn color="rgb(122,33,158)" @click="Return"
           style="
           color: white;
           float: left;
           margin-left: 4%;
           margin-top: 5%;">
      返回上页
    </v-btn>
    <v-alert outlined color="rgb(122,33,158)" class="layout" style="width:20%">
      <div class="text-h6">
        刀削面
      </div>
      <div>
        我可以十分确定，在别的地区，我没吃过和山西一样味道和口感的刀削面
      </div>
    </v-alert>
    <v-alert outlined color="rgb(122,33,158)" class="layout" style="width:20%">
      <div class="text-h6">
        驴肉甩饼
      </div>
      <div>
        反复提驴肉甩饼，就是为了说明：驴肉甩饼是长治美食天花板
      </div>
    </v-alert>
    <v-alert outlined color="rgb(122,33,158)" class="layout" style="width:20%">
      <div class="text-h6">
        延庆观炸鸡
      </div>
      <div>
        如果你有机会尝一尝，你一定会说比韩式炸鸡好吃
      </div>
    </v-alert>
    <v-btn color="rgb(122,33,158)" @click="Next"
           style="
           color: white;
           float: left;
           margin-left: 5%;
           margin-top: 5%;">
      探索长治
    </v-btn>
  </div>
</template>

<script>
export default {
  name: "Intro",
  data:()=>({
    path1:'/introduce',
    path2:'/entertainment'
  }),
  methods:{
    Return(){
      this.$router.replace(this.path1);
    },
    Next(){
      this.$router.replace(this.path2);
    }
  }
}
</script>

<style scoped>
.layout{
  float: left;
  margin-left: 5%;
  margin-top: 2.5%;
}
</style>



