<template>
  <v-card class="mx-auto" max-width="550">
    <v-card-text>
      <p class="text-h5 text--primary">我喜欢吃的美食</p>
      <div class="text-h6 text--primary">
        驴肉甩饼
      </div>
    </v-card-text>
    <v-card-actions>
      <v-btn text color="rgb(122,33,158)" @click="reveal = true">
        了解更多
      </v-btn>
    </v-card-actions>

    <v-expand-transition>
      <v-card v-if="reveal" class="transition-fast-in-fast-out v-card--reveal"
        style="height: 100%;">
        <v-card-text class="pb-0">
          <p class="text-h5 text--primary">
            驴肉甩饼
          </p>
          <p>起源于潞城，又称“潞城甩饼”。我相信每一个长治人一定都喜欢吃，这是长治美食天花板，
          现在迫不及待想放假回家吃</p>
        </v-card-text>
        <v-card-actions class="pt-0">
          <v-btn text color="rgb(122,33,158)" @click="reveal = false">
            关闭
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-expand-transition>
  </v-card>
</template>

<script>
export default {
  name: "CardRevealFood",
  data: () => ({
    reveal: false,
  }),
}
</script>

<style scoped>
.v-card--reveal {
  bottom: 0;
  opacity: 1 !important;
  position: absolute;
  width: 100%;
}
</style>



