import Vue from 'vue'
import Router from 'vue-router'
import ShowIndex from "../views/ShowIndex";
import Introduce from "../views/Introduce";
import Delicacy from "../views/Delicacy";
import Entertainment from "../views/Entertainment";

Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'',
      redirect:'/login'
    },
    {
      path:'/login',
      component:ShowIndex
    },
    {
      path:'/introduce',
      component:Introduce
    },
    {
      path:'/delicacy',
      component:Delicacy
    },
    {
      path:'/entertainment',
      component:Entertainment
    },
  ]
})



