<template>
  <v-main>
    <v-card class="mx-auto" max-width="500" elevation="6" ripple>
      <v-img :src="require('../assets/luding.jpg')" height="300px"></v-img>
      <v-card-title>长治</v-card-title>
      <v-card-subtitle>
        山西省地级市
      </v-card-subtitle>
      <v-card-actions>
        <v-btn text @click="ReturnLogin">返回登录</v-btn>
        <v-btn color="rgb(122,33,158)" text @click="Explore">探索长治</v-btn>
        <v-spacer></v-spacer>
        <v-btn icon @click="show = !show">
          <v-icon class="material-icons-outlined">
          {{ show ? 'expand_less' : 'expand_more' }}
          </v-icon>
        </v-btn>
      </v-card-actions>

      <v-expand-transition>
        <div v-show="show">
          <v-divider></v-divider>
          <v-card-text>
            长治市位于山西省东南部,东倚太行山，西屏太岳山。<br>
            古称上党、潞州、潞安等, “长治”原为潞安府府治所在县名，得名于明嘉靖八年（公元1529年），取
            <span style="color: rgb(122,33,158)">“长治久安”</span>之意。<br><br>

            这个信息卡的封面图片，是长治最繁华的地方之一：潞鼎广场。<br>
            就像图片中展示的一样，晚上很适合来此处闲逛。<br><br>

            下面的地图中，阴影部分即为山西的地域，而阴影部分中有一个显眼的
            <span style="color: rgb(122,33,158)">白色小点</span>， 这个点就是
            <span style="color: rgb(122,33,158)">长治市在山西省大致所处的位置</span>
            <br><br>
            <v-img :src="require('../assets/map.png')" height="300px"></v-img>
          </v-card-text>
        </div>
      </v-expand-transition>
    </v-card>
  </v-main>
</template>

<script>
export default {
  name: "IntroduceText",
  data: () => ({
    show: false,
    path1:'/login',
    path2:'/delicacy',
  }),
  methods:{
    ReturnLogin(){
      this.$router.replace(this.path1);
    },
    Explore(){
      this.$router.replace(this.path2);
    }
  }
}
</script>

<style scoped>

</style>



