<template>
  <v-main>
    <v-container fluid>
      <v-row align="center" justify="center">
        <v-col cols="12" sm="1" md="4" align="center" justify="center">
          <div class="head-title">
            <span>学 生 信 息</span>
          </div>
          <div class="sec-title">请使用SSPU统一认证的学号和密码登录</div>
          <v-card-text>
            <v-form autocomplete="off">
              <v-text-field label="姓名" name="Sna" height="60px" outlined
                            prepend-icon="mdi-account" type="text" value="刘玉堃">
              </v-text-field>
              <v-text-field label="学号" name="Sno" height="60px" outlined
                            prepend-icon="mdi-account" type="text" value="20201111233">
              </v-text-field>
              <v-text-field label="为了展示效果，随便输入点内容再登录，好吗" name="num" height="60px" outlined
                            prepend-icon="mdi-account" type="text">
              </v-text-field>
            </v-form>
          </v-card-text>

          <v-btn color="rgb(122,33,158)" height="45px" width="135px" rounded>
            <span class="button-content" @click="LoginClick">登&nbsp&nbsp录</span>
          </v-btn>
        </v-col>
      </v-row>
    </v-container>
  </v-main>
</template>

<script>
export default {
  name: "EnterForm",
  data: () => ({
    path: '/introduce'
  }),
  methods: {
    LoginClick() {
      this.$router.replace(this.path);
    }
  }
}
</script>

<style scoped>
  .head-title{
    margin-left: 12px;
    margin-top: 95px;
    margin-bottom: 30px;
    font-weight:bold;
    font-size:250%;
  }
  .sec-title{
    margin-top: 9%;
    margin-bottom: 15px;
  }
  .button-content{
    font-weight:bold;
    font-size:170%;
    color:white;
  }
</style>



