<template>
  <v-card class="mx-auto" max-width="550">
    <v-card-text>
      <p class="text-h5 text--primary">我喜欢去的地方</p>
      <div class="text-h6 text--primary">
        潞鼎广场
      </div>
    </v-card-text>
    <v-card-actions>
      <v-btn text color="rgb(122,33,158)" @click="reveal = true">
        了解更多
      </v-btn>
    </v-card-actions>

    <v-expand-transition>
      <v-card v-if="reveal" class="transition-fast-in-fast-out v-card--reveal"
        style="height: 100%;">
        <v-card-text class="pb-0">
          <p class="text-h5 text--primary">
            潞鼎广场
          </p>
          <p>近几年才修建好，晚上的灯光让我仿佛身处一线城市中心，可以喝喝咖啡，吃吃火锅，遛遛弯，很轻松惬意</p>
        </v-card-text>
        <v-card-actions class="pt-0">
          <v-btn text color="rgb(122,33,158)" @click="reveal = false">
            关闭
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-expand-transition>
  </v-card>
</template>

<script>
export default {
  name: "CardRevealPlay",
  data: () => ({
    reveal: false,
  }),
}
</script>

<style scoped>
.v-card--reveal {
  bottom: 0;
  opacity: 1 !important;
  position: absolute;
  width: 100%;
}
</style>



