<template>
  <v-card class="mx-auto" max-width="550">
    <v-card-text>
      <p class="text-h5 text--primary">我喜欢吃的美食</p>
      <div class="text-h6 text--primary">
        猜猜是什么
      </div>
    </v-card-text>
    <v-card-actions>
      <v-btn text color="rgb(122,33,158)" @click.stop="dialog = true">
        点击左边“显示”按钮
      </v-btn>
      <v-dialog v-model="dialog" max-width="290">
        <v-card>
          <v-card-title class="headline">再给你一次机会</v-card-title>
          <v-card-text>
            不是点这个哦~
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="rgb(122,33,158)" text @click="dialog = false">返回</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-card-actions>
  </v-card>
</template>

<script>
export default {
  name: "CardRevealFoodNull",
  data: () => ({
    reveal: false,
    dialog: false,
  }),
}
</script>

<style scoped>

</style>



