<template>
  <v-card min-width="550" elevation="5" ripple>
    <v-app-bar color="rgb(122,33,158)">
      <v-toolbar-title>
        <span class="left-title">长治特色</span>
      </v-toolbar-title><v-spacer></v-spacer>
    </v-app-bar>
    <v-container>
      <v-row>
        <v-col cols="12">
          <card-reveal-food></card-reveal-food>
        </v-col>
        <v-col cols="12">
          <card-reveal-play></card-reveal-play>
        </v-col>
      </v-row>
    </v-container>
  </v-card>
</template>

<script>
import CardRevealFood from "./CardRevealFood";
import CardRevealPlay from "./CardRevealPlay";
export default {
  name: "HorizontalCards",
  components: {
    CardRevealPlay,
    CardRevealFood
  },
}
</script>

<style scoped>
.left-title{
  color: white;
  font-size: 100%;
  margin-left: 20px;
}
</style>



