<template>
  <v-main>
    <div class="right-pos">
      <v-btn @click="hidden = !hidden" color="rgb(122,33,158)"
             large elevation="3" text fixed>
        {{ hidden ? '显示' : '隐藏' }}
      </v-btn>
      <div class="right-part" v-show="!hidden">
        <horizontal-cards></horizontal-cards>
      </div>
      <div class="right-part" v-show="hidden">
        <v-card min-width="550" elevation="5" ripple>
          <v-app-bar color="rgb(122,33,158)">
            <v-toolbar-title>
              <span class="left-title">长治特色</span>
            </v-toolbar-title><v-spacer></v-spacer>
          </v-app-bar>
          <v-container>
            <v-row>
              <v-col cols="12">
                <card-reveal-food-null></card-reveal-food-null>
              </v-col>
              <v-col cols="12">
                <card-reveal-play-null></card-reveal-play-null>
              </v-col>
            </v-row>
          </v-container>
        </v-card>
      </div>
    </div>
  </v-main>
</template>

<script>
import HorizontalCards from "./HorizontalCards";
import CardRevealFoodNull from "./CardRevealFoodNull";
import CardRevealPlayNull from "./CardRevealPlayNull";

export default {
  name: "ShowAndHide",
  components: {
    CardRevealFoodNull,
    CardRevealPlayNull,
    HorizontalCards

  },
  data: () => ({
    hidden: true,
  })
}
</script>

<style scoped>
.right-part{
  width: 50%;
  margin-left: 25%;
  margin-top: -36%;
}
.right-pos{
  position: fixed;
  margin-top: 20%;
  margin-left: 4%;
}
.left-title{
   color: white;
   font-size: 100%;
   margin-left: 20px;
 }
</style>



