<template>
  <v-main class="back">
    <div>
      <TopNav><span class="left-title">我的家乡：山西省长治市</span></TopNav>
      <IntroduceText class="left-part"></IntroduceText>
      <show-and-hide></show-and-hide>
    </div>
  </v-main>
</template>

<script>
import TopNav from "../components/TopNav";
import IntroduceText from "../components/IntroduceText";
import ShowAndHide from "../components/ShowAndHide";

export default {
  name: "Introduce",
  components:{
    ShowAndHide,
    TopNav,
    IntroduceText,
  }
}
</script>

<style scoped>
  .left-title {
    color: white;
    font-size: 130%;
    margin-left: 20px;
  }
  .left-part{
    float: left;
    margin-top: 7%;
    margin-left: 10%;
    margin-bottom: 7%;
  }
  .back{
    background-color: rgb(250,247,243);
    height: 100%;
    width: 100%;
  }
</style>



