<template>
  <v-row>
    <v-col>
      <v-card width="400" elevation="6">
        <v-img height="200px" :src="require('../assets/cover.jpg')">
          <v-toolbar flat color="rgba(0, 0, 0, 0)">
            <v-app-bar-nav-icon color="white" @click.stop="dialog = true">
              <span class="material-icons">favorite_border</span>
            </v-app-bar-nav-icon>
            <v-dialog v-model="dialog" max-width="350">
              <v-card>
                <v-card-title class="headline">有钱的话可以打赏一下作者</v-card-title>
                <v-card-text>
                  <img src="../assets/weixin.jpg" width="70%" style="margin-left: 15%">
                </v-card-text>
                <v-card-actions>
                  <v-btn color="rgb(122,33,158)" text @click="dialog = false">右上角的两个金币按钮有惊喜</v-btn>
                  <v-spacer></v-spacer>
                  <v-btn color="rgb(122,33,158)" text @click="dialog = false">返回</v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
            <span class="text-h6 white--text pl-0">Summary</span>
            <v-spacer></v-spacer>
          </v-toolbar>

          <v-card-title class="white--text mt-8">
            <v-avatar size="56">
              <img alt="user" src="../assets/headportrait.jpg">
            </v-avatar>
            <p class="ml-3">
              SolitudeFate
            </p>
          </v-card-title>
        </v-img>
        <v-card-text>
          <div class="font-weight-bold ml-8 mb-2">
            Catalogue
          </div>
          <v-timeline align-top dense>
            <v-timeline-item v-for="message in messages" :key="message.author" :color="message.color" small>
              <div>
                <div class="font-weight-normal">
                  <strong>{{ message.from }}</strong> ©{{ message.author }}
                </div>
                <div>{{ message.message }}</div>
              </div>
            </v-timeline-item>
          </v-timeline>
        </v-card-text>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
export default {
  name: "Summary",
  data: () => ({
    messages: [
      {
        from: 'Login',
        message: '在这里进行登录',
        author: 'SolitudeFate',
        color: 'rgb(122,33,158)',
      },
      {
        from: 'Introduce',
        message: '简要描述长治，以及我的喜好',
        author: 'SolitudeFate',
        color: 'green',
      },
      {
        from: 'Delicacy',
        message: '介绍了长治当地的特色美食',
        author: 'SolitudeFate',
        color: 'rgb(122,33,158)',
      },
      {
        from: 'Entertainment',
        message: '介绍了长治当地的特色地点',
        author: 'SolitudeFate',
        color: 'green',
      },
    ],
    dialog: false,
  }),
}
</script>

<style scoped>

</style>



